<template>
  <div id="app">
    <!--   -->    
    <van-nav-bar
      :title="title"
      fixed
      v-show="navViewShow"
    />
    <!--  -->
    <router-view @onTitle='getTitle' @onShow='getShow'></router-view>

    <!--  -->
    <!-- <TarbarView /> -->
    <van-tabbar v-model="active" active-color="#f44336" @change='onClickTabbarItem'>
      <van-tabbar-item icon="home-o" to='/'></van-tabbar-item>
      <van-tabbar-item icon="filter-o" to='/assor'></van-tabbar-item>
      <van-tabbar-item icon="search" to='/search'></van-tabbar-item>
      <van-tabbar-item icon="cart-o" to='/cart' info="20"></van-tabbar-item>
      <van-tabbar-item icon="contact" to='/mine'></van-tabbar-item>
    </van-tabbar>
  </div>
</template>


<script>
// import { useStore } from 'vuex';
import { 
  NavBar,
  Tabbar,
  TabbarItem 
  } from 'vant'

export default {
  name: 'App',
  components: {    
    // TarbarView,
    [NavBar.name] : NavBar,
    [Tabbar.name] : Tabbar,
    [TabbarItem.name] : TabbarItem,
  },
  data(){
    return {
      title:' ',
      navViewShow: true,
      active: 0,
    }
  },
  methods:{
    getTitle(title) {      
      this.title = title;
    },
    getShow(show){
      this.navViewShow = show;      
    },
    /*  navbar  */

    onClickTabbarItem(){
      // 
      window.localStorage.setItem('itemIndex', this.active)
    }
  },
  created(){
    // 
    var itemIndex = parseInt(window.localStorage.getItem('itemIndex'));
    // console.log(itemIndex);

    if(!isNaN(itemIndex)){
      this.active = itemIndex

      // 
      var toPath = this.$router.options.routes[itemIndex + 1].path
      
      // 
      this.$router.push({path: toPath})
      // this.$router.push({path:''})
      // console.log(this.$router.options.routes)
    }
  },
}
</script>

