<template>
    <div class="search-view">
        
       <van-search
            v-model="searchKey"            
            show-action
            label=""
            background="#f44336"
            placeholder=""
            @search="onSearch"
            >
            <template #action>
                <div @click="onSearch"></div>
            </template>
        </van-search>

        <div class="history-view">
            <div class="history-title">
                <h4></h4>
                <span @click="onClear"></span>
            </div>
            <van-tag round type="primary" 
            v-for="(item,index) in historyList"
            @click="onHistoryItem(item)"
            :key='index'>{{ item }}</van-tag>            
        </div>

        <!--  -->
        <SearchDetailView v-if="searchDetailShow" :searchDataList="searchDataList" @onBack='onDetailHide'></SearchDetailView>
        
    </div>
</template>

<script>
import { ref , reactive ,onBeforeUpdate, computed } from 'vue';
import { Search , Tag ,Toast } from 'vant';
import axios from 'axios'
import SearchDetailView from '../SearchDetailView/SearchDetailView.vue';


export default {
    name:'searchView',
    components:{
        [Search.name]: Search,
        [Tag.name]: Tag,
        SearchDetailView,
        [Toast.name]: Toast
    },
    setup( _ ,{ emit }  ){
        const navViewShow = false;
        const searchKey = ref([]);
        // const historyList = computed(() => {
        //     return `[,,]`
        // })
        const historyList = ref([
            '','',''
        ]);
        const searchDetailShow = ref(false);
        let searchDataList = ref([]);

        
        /*  */
        const onSearch = () =>{
            // console.log(searchKey.value);
            if(searchKey.value){
                searchDetailShow.value = true
                searchDetail()
                historyList.value.push(searchKey.value)
            }
            
        }

        const onClear = () =>{
            historyList.value = []
        }
        const onHistoryItem = (item) =>{
            // console.log(item)
            searchKey.value = item
            searchDetail()
        }

        const onDetailHide = () =>{
            searchDetailShow.value = false
        }

        const closeLoading = () =>{
            //   
            searchDetailShow.value = true
        }
        
        const searchDetail = () =>{
            //  
            Toast.loading({
                message: '...',
                forbidClick: true,
                overlay:true,
                duration: 0,
                onClose : closeLoading()
            });

            
            // 

            // 
            // console.log('' + searchKey.value)
            axios.get(`${process.env.VUE_APP_SERVER_URL}/main/json/search-detail.json`)
            .then(response => {
                searchDataList.value = response.data.list

                console.log(searchDataList.value)
                // console.log(searchDataList._rawValue)
                // 
                Toast.clear()
            })
        }

        // nextTick(() =>{
        //     onClear()
        // })

        // onBeforeUpdate(() => {
        //     console.log(historyList)
        //     // searchKey.value = [];
        //     // console.log(searchKey.value)
        // });


        return {
            navViewShow,
            searchKey,
            onSearch,
            onClear,
            historyList,
            onHistoryItem,
            searchDetailShow,
            onDetailHide,
            searchDataList
        };
    },
    // methods:{
    //     onSearch(val){
    //         console.log(val)
    //     }
    // },
    
    created() {
        this.$emit('onShow',this.navViewShow)
    }
}
</script>

