<template>
    <div class="edit-addr-view">
       <van-address-edit
        :area-list="areaList"
        :address-info="editValue"
        show-set-default
        show-search-result
        :search-result="searchResult"
        @save="onSave"
        @change-detail="onChangeDetail"
        />
    </div>
</template>

<script>
import AreaList from './area.js';
import { ref ,reactive } from 'vue';
import { AddressEdit ,Area } from 'vant';

export default {
    name:'EditAddrView',
    components:{
        [AddressEdit.name]: AddressEdit,
    },
    props:['editValue'],
    setup(_,{ emit }) {
        const searchResult = ref([]);
        const areaList = AreaList;
        /*
            address: " 138  7  501 "
            addressDetail: " "
            areaCode: "110101"
            city: ""
            country: ""
            county: ""
            id: "1"
            isDefault: false
            name: ""
            postalCode: ""
            province: ""
            tel: "13000000000" 
        */        

        const onSave = (content) => {
            const new_addr = reactive({
                "id": content.id,
                "name": content.name,
                "tel": content.tel,
                "address": content.city + content.county + content.addressDetail
            })
            
            emit('onSaveAddr',new_addr)
            // console.log(new_addr)
        };
        
        const onChangeDetail = (val) => {
        if (val) {
            searchResult.value = [
            {
                name: '',
                address: '',
            },
            {
                name: '',
                address: '',
            },
            ];
        } else {
            searchResult.value = [];
        }
        };

        return {
            onSave,
            areaList,
            searchResult,
            onChangeDetail,
        };
    },
}
</script>